import React, { useEffect } from "react";
import { useParams, useHistory } from "react-router-dom";

export default function Token() {
  let { token } = useParams();
  let history = useHistory();
  useEffect(() => {
    localStorage.setItem("hotok", token);
    history.push(`/`);
  }, [history, token]);
  return (
    <div>
      <h1>Redireccionando</h1>
    </div>
  );
}
